<template>
  <div class="db-rightBar" :class="{'hide':isHide}">
    <div class="db-rightBar__inner">
      
    </div>
    <div @click="$emit('close')" class="db-rightBar__overlay"></div>
  </div>
</template>

<script>

export default {
  name: 'db-sidebar',
  props: {
    isHide: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="scss" scoped>
  .db-rightBar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1200;

    &.hide {
      z-index: -20;
      .db-rightBar__overlay {
        opacity: 0;
        visibility: hidden;
      }
      .db-rightBar__inner {
        width: 0;
      }
    }

    &__overlay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0,0,0,0.3);
    }

    &__inner {
      position: fixed;
      top: 0;
      right: 0;
      height: 100%;
      width: 200px;
      background: #1D2939;
      z-index: 1250;
      transition: .5s all ease;
    }
  }
</style>